<template>
    <v-container id="template" >
        <v-btn id="print" class="blue white--text mb-6" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="ml-4 red white--text mb-6" to="/produksiharian" link>Back</v-btn>
       <template id="template" width="340px">
           <p class="text-left" style="font-size:8pt"><b>Rekap Pemakaian Bahan Produksi Tanggal : {{isi["Tanggal"]}}</b></p>
            <table id="table"  >
                <thead>
                    <tr style="font-size:9pt">
                        <th id="td">Kode Barang</th>
                        <th id="td">Nama Barang</th>
                        <th id="td">Jumlah Pemakaian</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in isi" :key='item.id'>
                        <td id="td" style="font-size:8pt; width:75px;padding-left:0.1cm;">{{item["Kode Barang"]}}</td>
                        <td id="td" style="font-size:8pt; width:151px;padding-left:0.1cm;" class="text-left">{{item["Nama Barang"]}}</td>
                        <td id="td" style="font-size:8pt; width:113px;padding-right:0.1cm;" class="text-right">{{item["SumOfKeluar"]}}</td>
                    </tr>
                </tbody>
            </table>
       </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                keyword: '',
                load: false,
                induk: [],
                isi: [],
                harian: new FormData
            }
        },
        methods: {
            async getData() {
                var uri = this.$apiUrl + 'produksis/dataharian/' + this.$route.params.kode
                var res1 = await this.$http.get(uri)
                this.induk = res1.data
                for (var i in this.induk) {
                    this.harian = new FormData
                    this.harian.append('tanggal', this.induk[i]["Tanggal"])
                    this.harian.append('kodeBarang', this.induk[i]["Kode Barang"])
                    this.harian.append('noSuplier', '')
                    this.harian.append('noBukti', '')
                    this.harian.append('keterangan', 'Produksi')
                    this.harian.append('masuk', this.induk[i]['JumlahJadi'])
                    this.harian.append('keluar', 0)
                    var uri = this.$apiUrl + 'pemakaians'
                    await this.$http.post(uri, this.harian)
                    if (this.induk[i]["Tutup"] != 'KOSONG') {
                        console.log("masuk paling atas")
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Tutup"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Etiket Depan"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Etiket Depan"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Etiket Belakang"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Etiket Belakang"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Etiket Tambahan"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Etiket Tambahan"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Botol"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Botol"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Dos"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Dos"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['JumlahDos'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Label"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Label"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['JumlahDos'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Isi"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Isi"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['JumlahIsi'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                }
                var uri = this.$apiUrl + 'pemakaians/laporanpemakaianharian'
                var y = await this.$http.get(uri)
                this.isi = y.data
                this.isi["Tanggal"] = y.data[0]["Tanggal"]
            },

            
            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style>
    @media print{
        #print{
            display: none;
        }
        #template{
            background-color: white
        }
    }
    
    #table, #th, #td {
        border: 1px solid black;
        border-collapse: collapse;
    }
</style>